.ecoButton {
    text-align: center;
}

.ecoButton div {
    margin-top: 15px;
}

.ecoButton div, .ecoButton div a{
    width: 100%;
}

.logoButton {
    padding: 5px;
    color: #000;
    border-color: #fdd340!important;
    transition: all 0.5s;
}

.logoButton:hover {
    -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
    -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
}

.ecoWord {
    color: #fdd340;
}

.logoButton img {
    width: 25px;
}